.block {
  display: flex;
}

.link-btn {
  display: flex;
  align-items: center;
  background: none;
  font-family: "Roboto";
  color: #fff;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  text-decoration: none;
  font-size: 0;
}

@media only screen and (max-width: 767px) {
  .link-btn {
    height: 30px;
    color: #000DFF;
  }
}

.no-touch .link-btn:hover .link-btn__icon {
  transform: translateX(3px);
}

.link-btn__text {
  font-size: 12px;
}

.link-btn__icon {
  position: relative;
  margin-left: 5px;
  transition: transform .3s;
}

.link-btn__icon .icon {
  font-size: 12px;
}

.social-link {
  background: none;
  display: inline-block;
}

.social-link .icon {
  font-size: 24px;
  color: #4a484a;
}

.header {
  background: linear-gradient(135deg, #000DFF 10%, #6B73FF 100%);
  color: #fff;
  padding: 20px 0;
}

@media only screen and (max-width: 767px) {
  .header {
    padding: 15px 0;
  }
}

.header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

@media only screen and (max-width: 767px) {
  .header__name {
    flex: 0 0 100%;
    max-width: 100%;
  }
}

.header__name > *:not(:last-child) {
  margin-bottom: 6px;
}

@media only screen and (max-width: 767px) {
  .header__name > *:not(:last-child) {
    margin-bottom: 0;
  }
}

@media only screen and (max-width: 767px) {
  .header__links {
    padding: 10px 0;
  }
}

.social-links {
  align-items: center;
}

.social-link {
  text-decoration: none;
  color: inherit;
  font-size: 0;
  display: block;
}

.no-touch .social-link:hover span:after {
  transform: scaleX(1);
  transform-origin: 0 0;
}

.social-link span {
  font-size: 14px;
  line-height: 1.2;
  display: inline-block;
  position: relative;
  overflow: hidden;
}

.social-link span:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  height: 1px;
  width: 100%;
  background-color: #fff;
  transform: scaleX(0);
  transition: transform .4s;
  transform-origin: 100% 0;
}

.portfolio-item {
  position: relative;
  color: #fff;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.15);
  transition: box-shadow 0.3s;
}

@media only screen and (max-width: 599px) {
  .portfolio-item {
    color: #4a484a;
    padding: 0;
    box-shadow: none;
  }
}

.no-touch .portfolio-item:hover {
  box-shadow: 0 11px 43px 0 rgba(7, 42, 68, 0.3);
}

@media only screen and (max-width: 767px) {
  .no-touch .portfolio-item:hover {
    box-shadow: none;
  }
}

.no-touch .portfolio-item:hover .portfolio-item__image {
  transform: scale(1.04);
}

.no-touch .portfolio-item:hover .portfolio-item__image:before {
  opacity: 0.95;
}

.no-touch .portfolio-item:hover .portfolio-item__content {
  opacity: 1;
  visibility: visible;
}

.portfolio-item__image-wrapper {
  overflow: hidden;
}

@media only screen and (max-width: 599px) {
  .portfolio-item__image-wrapper {
    position: relative;
    margin-bottom: 10px;
  }
}

.portfolio-item__image {
  padding-bottom: 65%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  transition: transform 0.6s;
  backface-visibility: hidden;
}

.portfolio-item__image:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  background: linear-gradient(to left, #36d1dc, #5b86e5);
  transition: opacity 0.6s;
}

@media only screen and (max-width: 599px) {
  .portfolio-item__image:before {
    display: none;
  }
}

.portfolio-item__content {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 30px 20px 20px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
  z-index: 5;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.6s, visibility 0.6s;
}

@media only screen and (max-width: 1259px) {
  .portfolio-item__content {
    padding: 20px 20px 10px;
  }
}

@media only screen and (max-width: 599px) {
  .portfolio-item__content {
    position: relative;
    padding: 0;
    opacity: 1;
    visibility: visible;
  }
}

.portfolio-item .link-btn:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

@media only screen and (max-width: 767px) {
  .portfolio-item .link-btn:after {
    display: none;
  }
}

.portfolio-item__title {
  margin-bottom: 16px;
}

@media only screen and (max-width: 599px) {
  .portfolio-item__title {
    margin-bottom: 4px;
  }
}

.portfolio-item__descr {
  font-size: 12px;
  max-width: 190px;
  margin-bottom: 12px;
}

@media only screen and (max-width: 1259px) {
  .portfolio-item__descr {
    max-width: 250px;
  }
}

@media only screen and (max-width: 599px) {
  .portfolio-item__descr {
    max-width: none;
  }
}

.portfolio-item__descr p:not(:last-child) {
  margin-bottom: 6px;
}

@media only screen and (max-width: 767px) {
  .portfolio-item__descr p:not(:last-child) {
    margin-bottom: 3px;
  }
}

.portfolio {
  display: flex;
  flex-wrap: wrap;
}

@media only screen and (max-width: 1023px) {
  .portfolio {
    justify-content: center;
  }
}

.portfolio__item {
  padding-bottom: 30px;
}

@media only screen and (max-width: 599px) {
  .portfolio__item {
    padding-bottom: 40px;
  }
  .portfolio__item:last-child {
    padding-bottom: 0;
  }
}
