.portfolio-item {
	position: relative;
	color: $white;
	box-shadow: 0 2px 4px 0 rgba(0,0,0,.15);
	transition: box-shadow 0.3s;
	@include xs {
		color: $text;
		padding: 0;
		box-shadow: none; }
	@include hover {
		box-shadow: 0 11px 43px 0 rgba(7,42,68,.3);
		@include sm {
			box-shadow: none; }
		.portfolio-item__image {
			transform: scale(1.04);
			&:before {
				opacity: 0.95; } }
		.portfolio-item__content {
			@include fadein; } }
	&__image-wrapper {
		overflow: hidden;
		@include xs {
			position: relative;
			margin-bottom: 10px; } }
	&__image {
		padding-bottom: 65%;
		@include cover;
		transition: transform $trLong;
		backface-visibility: hidden;
		&:before {
			content: '';
			@include coverdiv;
			opacity: 0;
			background: $portfolioGrad;
			transition: opacity $trLong;
			@include xs {
				display: none; } } }
	&__content {
		@include coverdiv;
		padding: 30px 20px 20px;
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		justify-content: space-between;
		z-index: 5;
		@include fadeout($trLong);
		@include lg {
			padding: 20px 20px 10px; }
		@include xs {
			position: relative;
			padding: 0;
			@include fadein; } }
	.link-btn {
		&:after {
			content: '';
			@include coverdiv;
			@include sm {
				display: none; } } }
	&__title {
		margin-bottom: 16px;
		@include xs {
			margin-bottom: 4px; } }
	&__descr {
		font-size: 12px;
		max-width: 190px;
		margin-bottom: 12px;
		@include lg {
			max-width: 250px; }
		@include xs {
			max-width: none; }
		p {
			&:not(:last-child) {
				margin-bottom: 6px;
				@include sm {
					margin-bottom: 3px; } } } } }

.portfolio {
	display: flex;
	flex-wrap: wrap;
	@include md {
		justify-content: center; }
	&__item {
		padding-bottom: 30px;
		@include xs {
			padding-bottom: 40px;
			&:last-child {
				padding-bottom: 0; } } } }

